@font-face{

    src:url('../assets/fonts/SFMono-Regular.otf');
    font-family: "SFMono";

}
pre, code{
    background: #31343f !important;
    font-family: "SFMono";
}
.highlight{
    border-radius:5px;
}
.highlight,
pre.highlight {
    background: #31343f !important;
    color: #f8f8f2;
    box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
    border:none; 
}

